import Vue from "vue";

export default Vue.extend({
  name: "create-user",
  data: () => {
    let addUserData = {
      step: 1,
      phone: "",
      name: "",
      address: "",
      nextDisabled: false as boolean,
    };
    return addUserData;
  },
  methods: {
    next() {
      document.getElementById("next")?.click();
    },
    phoneChange() {
      if (!this.phone) return;
      let ph: string = this.phone;
      if (!(ph.startsWith("+") || ph.startsWith("0"))) {
        ph = "+91" + ph;
      }
      ph = (ph.startsWith("+") ? "+" : "") + ph.replace(/[^0-9]/g, "").trim();
      if (ph.startsWith("+91") && ph.length > 3) {
        ph = "+91 " + ph.slice(3, 13);
      }
      if (ph.startsWith("0") && ph.length > 5) {
        ph = ph.slice(0, 5) + "-" + ph.slice(5, 11);
      }
      setTimeout(() => {
        this.phone = ph;
      }, 0);
    },
    stepForward() {
      this.nextDisabled = true;
      this.validate((success) => {
        if (success) this.step++;
        this.nextDisabled = false;
      });
    },
    validate(fn: (success: boolean) => void) {
      if (this.step > 3) return fn(true);
      switch (this.step) {
        case 1:
          if (!this.name) return fn(false);
          return fn(true);
          break;
        case 2:
          if (!this.phone) return fn(false);
          window.ipcrenderer.once(
            "phone-exixts",
            (event, err: sqliteError, response: boolean) => {
              if (!response) {
                fn(true);
              } else fn(false);
            }
          );
          window.ipcrenderer.send("phone-exixts", this.phone);
          break;
        case 3:
          if (!this.address) return fn(false);
          return fn(true);
          break;
      }
    },
    submit() {
      window.ipcrenderer.once("create-user", function (
        event,
        err: sqliteError,
