      ) {
        alert("Created User \n" + inspect(data) + "\nError:\n" + err);
        window.close();
      });
      window.ipcrenderer.send("create-user", {
        name: this.name,
        phone: this.phone,
        address: this.address,
      });
    },
  },
  computed: {
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Add User";
        case 2:
          return "Phone Number of " + this.name;
        case 3:
          return "Address of " + this.name;
        case 4:
          return "Final";
      }
    },
  },
  components: {},
  mounted() {},
});
