
































































































import Vue from 'vue';

export default Vue.extend({
  name: 'create-group',
  data:()=>{return {
    step:1,
    details:{}
  }},
  computed:{
    currentTitle(){
      switch(this.step){
        case 1: return "Add Group"
        case 2: return "Add Someone"
        case 3: return "Add Address"
        case 4: return "Final"
      }
    }
  },
  components: {
  }
});
